import uniStatusBar from '../../components/uni-status-bar/uni-status-bar.vue';
import otherLogin from '../../components/home/other-login.vue';
export default {
	components: {
		uniStatusBar,
		otherLogin
	},
	data() {
		return {
			status: false, // false 代表账号密码登录， true 代表手机验证码登录
			disable: true,
			loading: false,
			userName: '',
			password: '',
			phone: '',
			checkNum: '',
			codetime: 0
		}
	},
	watch: {
		userName (val) {
			this.onBtnChange();
		},
		password (val) {
			this.onBtnChange();
		},
		phone (val) {
			this.onBtnChange();
		},
		checkNum (val) {
			this.onBtnChange();
		}
	},
	methods: {
		// 返回上一步
		back () {
			uni.navigateBack({
				delta: 1
			})
		},
		// 初始化表单
		initInput () {
			this.userName = '';
			this.password = '';
			this.phone = '';
			this.checkNum = '';
		},
		// 切换登录状态
		changeStatus () {
			this.initInput();
			this.status = !this.status;
			console.log(status)
		},
		// 改变按钮的状态
		onBtnChange () {
			if ((this.userName && this.password) || (this.phone && this.checkNum)) {
				this.disable = false;
				return;
			}
			this.disable = true;
		},
		// 验证手机号码合法性
		isPhone () {
			// 验证手机合法性
			let mPattern = /^1[34578]\d{9}$/;
			return mPattern.test(this.phone);
		},
		// 获取验证码
		getCheckNum () {
			if (this.codetime > 0) {
				uni.showToast({
					title: '不能重复获取',
					icon: 'none'
				})
				return;
			}
			
			// 验证手机合法性
			if(!this.isPhone()) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none'
				});
				return;
			}
			
			// 请求服务器，发送验证码
			// 发送成功，开启倒计时
			this.codetime = 60;
			let timer = setInterval(() => {
				this.codetime--;
				if(this.codetime < 1) {
					clearInterval(timer);
					this.codetime = 0;
				}
			}, 1000);
		}, 
		// 登录
		submit () {
			// 账号密码登录
			if (!this.status) {
				return;
			}
			// 验证码登录
			// 验证手机登录
			if(!this.isPhone()) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none'
				});
				return;
			}
		}
	}
}
